import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);
const store = new Vuex.Store({
  state: {
    level1show: true,
    level2show: false,
    level3show: false,
    exploreshow: {
      Ea1show: true,
      Ea2show: false,
      Ea3show: false,
      Ea4show: false,
      Ea5show: false,
      Ea6show: false
    },
    ip: "localhost"
  },
  mutations: {
    changestatus1(state) {
      state.level2show = false;
      state.level3show = false;
      state.level1show = !state.level1show;
    },
    changestatus2(state) {
      state.level1show = false;
      state.level3show = false;
      state.level2show = !state.level2show;
    },
    changestatus3(state) {
      state.level1show = false;
      state.level2show = false;
      state.level3show = !state.level3show;
    },
    changeexplore(state, obj) {
      if (!state.exploreshow[obj]) {
        for (const i in state.exploreshow) {
          if (state.exploreshow[i]) {
            state.exploreshow[i] = false;
          }
        }
      }
      state.exploreshow[obj] = !state.exploreshow[obj];
    }
  },
  actions: {},
  getters: {
    up3u1(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "up3u1")];
    },
    down3u1(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "down3u1")];
    },
    up3u2(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "up3u2")];
    },
    down3u2(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "down3u2")];
    },
    up3u3(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "up3u3")];
    },
    down3u3(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "down3u3")];
    },
    up3u4(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "up3u4")];
    },
    down3u4(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "down3u4")];
    },
    up4u1(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "up4u1")];
    },
    down4u1(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "down4u1")];
    },
    up4u2(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "up4u2")];
    },
    down4u2(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "down4u2")];
    },
    down4u3(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "down4u3")];
    },
    up4u3(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "up4u3")];
    },
    up4u4(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "up4u4")];
    },
    down4u4(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "down4u4")];
    },
    up5u1(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "up5u1")];
    },
    down5u1(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "down5u1")];
    },
    down5u2(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === " down5u2")];
    },
    up5u2(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === " up5u2")];
    },
    up5u3(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === " up5u3")];
    },
    down5u3(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === " down5u3")];
    },
    down5u4(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === " down5u4")];
    },
    up5u4(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === " up5u4")];
    },
    up6u1(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "up6u1")];
    },
    down6u1(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "down6u1")];
    },
    down6u2(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === " down6u2")];
    },
    up6u2(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === "up6u2")];
    },
    up6u3(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === " up6u3")];
    },
    down6u3(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === " down6u3")];
    },
    down6u4(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === " down6u4")];
    },
    up6u4(state, getters) {
      return [...state.wordlist.filter(item => item.gradeu === " up6u4")];
    }
  }
});
export default store;
