<template>
  <div class="school levellist">
    <div class="classbox">
      <div class="inner">
        <ul class="classlist">
          <li v-for="item in classlist" :key="item.id" @mouseleave="hide(item.id)">
            <div class="pic-box" @mouseenter="show(item.id)">
              <img :src="item.pic" alt style="width:90%" />
              <p class="classtitle" style="font-size:20px;">{{item.title}}</p>
            </div>
            <div class="underlist" v-show="item.show">
              <ul>
                <li @click="jump(`/school/${item.id}/unit1/`)">
                  <span>Unit1</span>
                </li>
                <li @click="jump(`/school/${item.id}/unit2/`)">
                  <span>Unit2</span>
                </li>
                <li @click="jump(`/school/${item.id}/unit3/`)">
                  <span>Unit3</span>
                </li>
                <li @click="jump(`/school/${item.id}/unit4/`)">
                  <span>Unit4</span>
                </li>
                <li @click="jump(`/school/${item.id}/review/`)">
                  <span>Review</span>
                </li>
              </ul>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "schoolbooklist",
  // 此处填写level1 的课程名称，以课程id 课程名称，课程背景图,课程视频地址组成
  data() {
    return {
      classlist: [
        {
          id: "up3",
          title: "third grade(1st)",
          pic: "http://dzkbw.com/books/jjb/yingyu/3s/Cover.jpg",
          show: false
        },

        {
          id: "up4",
          title: "fourth grade(1st)",
          pic: "http://dzkbw.com/books/jjb/yingyu/4s/Cover.jpg",
          show: false
        },
        {
          id: "up5",
          title: "fifth grade(1st)",
          pic: "http://dzkbw.com/books/jjb/yingyu/5s/Cover.jpg",
          show: false
        },
        {
          id: "up6",
          title: "sixth grade(1st)",
          pic: "http://dzkbw.com/books/jjb/yingyu/6s/Cover.jpg",
          show: false
        },
        {
          id: "down3",
          title: "third grade(2nd)",
          pic: "http://dzkbw.com/books/jjb/yingyu/3x/Cover.jpg",
          show: false
        },
        {
          id: "down4",
          title: "fourth grade(2nd)",
          pic: "http://dzkbw.com/books/jjb/yingyu/4x/Cover.jpg",
          show: false
        },

        {
          id: "down5",
          title: "fifth grade(2nd)",
          pic: "http://dzkbw.com/books/jjb/yingyu/5x/Cover.jpg",
          show: false
        },
        {
          id: "down6",
          title: "sixth grade(2nd)",
          pic: "http://dzkbw.com/books/jjb/yingyu/6x/Cover.jpg",
          show: false
        }
      ]
    };
  },

  methods: {
    jump(id) {
      window.location.href = `http://${this.$store.state.ip}:5000/${id}`;
    },
    show(id) {
      this.classlist.find(item => item.id === id).show = true;
      window.console.log(this.classlist.find(item => item.id === id));
    },
    hide(id) {
      this.classlist.find(item => item.id === id).show = false;
      window.console.log(this.classlist.find(item => item.id === id));
    }
  }
};
</script>

<style>
.school.levellist {
  padding-top: 30px;
}
.underlist {
  position: absolute;
  top: 0%;
  z-index: 300;
  left: 50%;
  transform: translate(-50%);
  background-color: rgba(299, 1, 19, 0.788);
  width: 53%;
}
.underlist {
  transition: 1s linear;
}
.underlist ul {
  justify-content: space-around;
  flex-direction: column;
}

.underlist > ul span {
  display: block;
  font-size: 20px;
  line-height: 2.5;
  color: aliceblue;
  font-weight: bolder;
  border-bottom: 1px solid rgb(255, 255, 255);
  width: 100%;
}
.underlist > ul li {
  width: 100%;
}
.underlist li:hover {
  background-color: rgb(105, 2, 2);
}
.classlist li {
  position: relative;
}
</style>
