<template>
  <div class="6A levellist">
    <p>
      <span style="flex-grow:1">6 A</span>
      <span
        class="icon iconfont icon-icon-test showmore"
        style="font-size:3rem;font-weight:bolder;line-height:1;flex-grow:0;
                                cursor: pointer;display:block;"
        @click="$store.commit('changeexplore','Ea6show')"
      >{{showlist?'▲':"▼"}}</span>
    </p>
    <div class="classbox" v-show="showlist">
      <div class="inner">
        <ul class="classlist">
          <li v-for="item in classlist" :key="item.id" @mouseleave="hide(item.id)">
            <div class="pic-box" @mouseenter="show(item.id)">
              <img :src="item.pic" alt style="width:90%" />
              <p class="classtitle" style="font-size:20px;">{{item.title}}</p>
            </div>
            <div class="underlist" v-show="item.show">
              <ul>
                <li @click="jump(`explore/a6/${item.id}/class1/`)">
                  <span>Unit1</span>
                </li>
                <li @click="jump(`explore/a6/${item.id}/class2/`)">
                  <span>Unit2</span>
                </li>
                <li @click="jump(`explore/a6/${item.id}/class3/`)">
                  <span>Unit3</span>
                </li>
                <li @click="jump(`explore/a6/${item.id}/class4/`)">
                  <span>Unit4</span>
                </li>
                <li @click="jump(`explore/a6/${item.id}/review/`)">
                  <span>Review</span>
                </li>
              </ul>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "A6e",
  // 此处填写1A 的课程名称，以课程id 课程名称，课程背景图,课程视频地址组成
  data() {
    return {
      classlist: [
        {
          id: "unit1",
          title: "Unit one",
          pic: require("./../../assets/image/LEVEL6.png"),
          show: false
        },
        {
          id: "unit2",
          title: "Unit two",
          pic: require("./../../assets/image/LEVEL6.png"),
          show: false
        },
        {
          id: "unit3",
          title: "Unit three",
          pic: require("./../../assets/image/LEVEL6.png"),
          show: false
        },
        {
          id: "unit4",
          title: "Unit four",
          pic: require("./../../assets/image/LEVEL6.png"),
          show: false
        },
        {
          id: "unit5",
          title: "Unit five",
          pic: require("./../../assets/image/LEVEL6.png"),
          show: false
        },
        {
          id: "unit6",
          title: "Unit six",
          pic: require("./../../assets/image/LEVEL6.png"),
          show: false
        }
      ]
    };
  },
  computed: {
    showlist() {
      return this.$store.state.exploreshow.Ea6show;
    }
  },
  methods: {
    jump(id) {
      window.location.href = `http://${this.$store.state.ip}:5000/${id}`;
    },
    show(id) {
      this.classlist.find(item => item.id === id).show = true;
      window.console.log(this.classlist.find(item => item.id === id));
    },
    hide(id) {
      this.classlist.find(item => item.id === id).show = false;
      window.console.log(this.classlist.find(item => item.id === id));
    }
  }
};
</script>

<style>
.classlist {
  margin: 0;
}
</style>
